* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; /* Ensure padding and borders don't increase the element's size */
}

.container {
  background: #000;
  min-height: 50vh;
  overflow: hidden;
  display: flex;
  flex-direction: column; /* Stack children vertically on smaller screens */
  align-items: center;
  justify-content: center;
}

.img {
  width: 50%; /* Adjust width to fill the container */
  max-width: 100%; /* Ensure the image doesn't exceed its natural width */
  height: 410px;
}

.wrapper {
  width: 100%;
  max-width: 90%; /* Adjust the maximum width for mobile screens */
  padding: 20px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

h1 {
  font-size: 30px; /* Adjust font size for mobile screens */
  font-weight: 600;
  color: #445A6F;
  margin: 20px 0;
}

input {
  width: 100%; /* Adjust input width to fill the container */
  max-width: 100%; /* Allow inputs to expand to full width */
  padding: 8px;
}

input:focus-visible {
  outline: none;
}

button {
  width: 100%; /* Adjust button width to fill the container */
  max-width: 150px;
  height: 40px;
  border-radius: 5px;
  background-color: #445A6F;
  border: 1px solid #445A6F;
  color: #fff;
  font-size: 16px;
  margin: 20px 0;
}

/* Default styles for the anchor tag */
a {
  text-decoration: dotted; /* Remove underlines from links */
  color: #0077FF; /* Set link color */
  transition: color 0.8s; /* Add a smooth color transition effect */
  font-weight: 700;
}

/* Styles for hover (mouse over) */
a:hover {
  color: green; /* Change link color on hover */
}

/* Media query for responsive design */



/* Media query for mobile view */
@media (max-width: 767px) {
  .wrapper {
    max-width: 100%; /* Allow full width for mobile screens */
  }
  h1 {
    font-size: 24px; /* Further reduce font size for smaller screens */
  }
  button {
    font-size: 14px; /* Further reduce font size for smaller screens */
  }
  .img{
    width: 100%;
    height: 350px;
  }
  a {
    font-size: 11px; /* Decrease font size for smaller screens */
    font-weight: 700;
  }
}
